/* make variables */
:root {
  --lightgray: #d9d9d9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkgray: #464d53;
  --calorycard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor: #3c3f45;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  /* overflow: hidden; */
}
.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow-x: hidden;
  /* overflow: hidden; */
}
::-webkit-scrollbar {
  display: none;
}
.container {
  max-width: 1440px;
  margin: 0 auto;
}
.stroke {
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  color: transparent;
  /* font-size: 3rem; */
}
.blur-circle {
  background-color: var(--orange);
  width: 500px;
  height: 600px;
  filter: blur(100px);
  z-index: -99;
  position: absolute;
  opacity: 0.3;
}
.width {
  width: 1000px;
}
.height {
  height: 200px;
  position: absolute;
  /* bottom: 0; */
}

@media screen and (max-width: 768px) {
  .width {
    width: 300px;
  }
}
@media screen and (max-width: 768px) {
  .blur-circle {
    background-color: var(--orange);
    width: 290px;
    height: 800px;
    filter: blur(100px);
    z-index: -99;
    position: absolute;
    opacity: 0.3;
  }
  .App {
    gap: 4.5rem;

    /* overflow: hidden; */
  }
}
@media screen and (max-width: 320px) {
  .blur-circle {
    background-color: var(--orange);
    width: 250px;
    height: 800px;
    filter: blur(100px);
    z-index: -99;
    position: absolute;
    opacity: 0.3;
  }
}
